.card{
    max-height: 15em;
    border: 1px solid black;
    padding: 0.1em;
}

.hand{
    max-height: 20em;
    display: block;
    text-align: center;
}

.actionContainer{
    width: 100%;
    text-align: center;
}

.actionButton{
    font-size: 4em;
    padding: 0.25em;
    width: 25%;
    margin-top: 1em;
}

.gameResults{
    text-align: center;
    font-size: 2em;
    margin: 1em;
}

#hitButton{
    background-color: rgb(0,255,0);
}

#stayButton{
    background-color: rgb(255,0,0);
}

#hitButton:hover{
    background-color: rgb(147, 224, 147);
}
#stayButton:hover{
    background-color: rgb(218, 160, 160);
}
#restartButton{
    width: 100%; 
    margin: 0;
}
@media only screen and (max-width: 650px) {
    .hand{
        overflow: scroll;
        width: 100%;
        text-align: left;
    }

    .cardContainer{
        width: 155%;
    }

    .card{
        max-height: 9em;
        border: 1px solid black;
        padding: 0.1em;
        overflow: scroll;
    }
    .actionButton{
        font-size: 3em;
        padding: 0.5em;
        width: 50%;
    }

    .gameResults{
        text-align: center;
        font-size: 1.5em;
        margin: 0.25em;
        margin-bottom: 0;
    }

    #restartButton{
        font-size: 3em;
    }
    
}

.actionButton{
    font-size: 4em;
    padding: 0.25em;
    width: 25%;
    margin-top: 1em;
}

.gameContainer{
    text-align: center;
}

.gameStartButton{
    font-size: 3em;
    text-align: center;
}

#restartButton{
    font-size: 2em;
}
.square{
    height: 5em;
    width: 5em;
    font-size: 2em;
    position: relative;
}

.squareText{
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
}
@media only screen and (max-width: 650px) {
    .board{
        margin-top: 5em;
    }
    .square{
        width: 3.5em;
        height: 3.5em;
        position: relative;
    }
    
    .squareText{
        position: absolute;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;
    }

    #restartButton{
        font-size: 1em;
    }
}

h1, h2, h3, h4, h5, h6, span, button{
    font-family: Arial, Helvetica, sans-serif;
}
.appHeader{
    font-size: 5em;
    text-align: center;
}
.gameSelectContainer{
    text-align: center;
}

.gameSelectButton{
    padding: 1em;
    font-size: 3em;
}

.statContainer{
    position: fixed;
    right: 1em;
    top: 1em;
    text-align: right;
}
.statValue{
    font-size: 2.5em;
    display: block;
}
.resetStatsButton{
    font-size: 2em;
    padding: 1em;
}
.gameStartButton{
    font-size: 3em;
    background-color: rgb(0, 255, 0);
    padding: 1em;
    display: block;
    margin-left: auto;
    margin-right: auto;
}
.gameContainer{
    margin-top: 6em;
    display: block;
}
@media only screen and (max-width: 650px) {
    .appHeader{
        text-align: left;
        font-size: 2.5em;
        margin-top: 0;
    }
    .statContainer{
        right: 0.1em;
    }
    .statValue{
        font-size: 1.25em;
    }
    .resetStatsButton{
        font-size: 1.25em;
        right: 0;
        margin-right: 0;
        margin-top: 0.5em;
        padding: 0.5em;
    }
    .gameSelectButton{
        padding: 3vw;
        font-size: 7vw;
    }
    .gameStartButton{
        font-size: 1.5em;
        padding: 0.75em;
        display: block;
        margin-left: 0;
    }
}
