
h1, h2, h3, h4, h5, h6, span, button{
    font-family: Arial, Helvetica, sans-serif;
}
.appHeader{
    font-size: 5em;
    text-align: center;
}
.gameSelectContainer{
    text-align: center;
}

.gameSelectButton{
    padding: 1em;
    font-size: 3em;
}

.statContainer{
    position: fixed;
    right: 1em;
    top: 1em;
    text-align: right;
}
.statValue{
    font-size: 2.5em;
    display: block;
}
.resetStatsButton{
    font-size: 2em;
    padding: 1em;
}
.gameStartButton{
    font-size: 3em;
    background-color: rgb(0, 255, 0);
    padding: 1em;
    display: block;
    margin-left: auto;
    margin-right: auto;
}
.gameContainer{
    margin-top: 6em;
    display: block;
}
@media only screen and (max-width: 650px) {
    .appHeader{
        text-align: left;
        font-size: 2.5em;
        margin-top: 0;
    }
    .statContainer{
        right: 0.1em;
    }
    .statValue{
        font-size: 1.25em;
    }
    .resetStatsButton{
        font-size: 1.25em;
        right: 0;
        margin-right: 0;
        margin-top: 0.5em;
        padding: 0.5em;
    }
    .gameSelectButton{
        padding: 3vw;
        font-size: 7vw;
    }
    .gameStartButton{
        font-size: 1.5em;
        padding: 0.75em;
        display: block;
        margin-left: 0;
    }
}